
import noData from '@/components/noData'
import back from '@/components/back'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
components: {
noData,
back
},
data(){
return {
type:parseInt(this.$route.query.type),
tabItem: ['7天', '30天', '90天'],
item: parseInt(this.$route.query.item) || -7,
active: 0,
list: [],
haveMore: true,
page: 1,
idx:0,
}
},
created() {
this.pull(false)
switch(parseInt(this.$route.query.item)){
case -7:
this.active = 0;
break;
case -30:
this.active = 1;
break;
case -90:
this.active = 2;
break;
}
},
mounted() {
this.getMore();
switch(parseInt(this.$route.query.type)){
case 1:
document.title = "血压"
break;
case 2:
document.title = "血糖"
break;
case 3:
document.title = "体脂"
break;
}
},
beforeDestroy() {
window.onscroll = () => {

}
},
methods: {
getDate(day) {

let time = new Date();
time.setDate(time.getDate() + day); 
let y = time.getFullYear();
let m = time.getMonth() + 1; 
let d = time.getDate();
return y + "-" + m + "-" + d;
},
getCurrentTime() {

let time = new Date();
let h = time.getHours();
let min = time.getMinutes();
let s = time.getSeconds();
return h + ":" + min + ":" + s;
},
pull(h) {
let url = "";
switch(parseInt(this.$route.query.type)){
case 1:
url = '/MonitorAPI/Monitoring/GetJC_BloodPressureList'
break;
case 2:
url = '/MonitorAPI/Monitoring/GetJC_BloodSugarDataList'
break;
case 3:
url = '/MonitorAPI/Monitoring/GetJC_BodyFatDataList'
break;
}
if(this.haveMore) {
this.$post(url,{
PatientId: this.$route.query.PatientId,
StartTime:this.getDate(this.item) + " 00:00:00",
EndTime:this.getDate(0) + " " + this.getCurrentTime(),
PageIndex:this.page,
PageSize:10,
}).then(res => {

if (!res.ReturnData || res.ReturnData.length < 10){
this.haveMore = false
}
else {
this.haveMore = true
}
this.list = res.ReturnData || []
this.idx++;
})
}
},
tabOn(idx){
this.active = idx
this.haveMore = true
this.page = 1
this.list = []
switch(idx){
case 0:
this.item = -7;
break;
case 1:
this.item = -30;
break;
case 2:
this.item = -90;
break;
}
this.pull(false)
},
getMore(){
const elist = this.$refs.elist
let timer = null
window.onscroll = () => {
clearTimeout(timer)
timer = setTimeout(() => {
const top = document.documentElement.scrollTop
|| window.pageYOffset
|| document.body.scrollTop,
h = elist.scrollHeight,
wh = window.innerHeight

if (h - top - wh < 100 && this.haveMore){
this.page += 1
this.pull(true)
}
}, 100)
}
},
detailsLine(){

if(!this.list || this.list.length == 0){
AlertModule.show({
content: "暂无数据",
})
/*this.$vux.alert.show({
content: "暂无数据",
})*/
}else{
this.$router.push({
path: '/healthItemL',
query: {
PatientId: this.$route.query.PatientId,
item:this.item,
type:this.$route.query.type
}
});
}

}
}
}



